import React, { Component, Fragment } from 'react';
import Page from 'components/LayoutComponents/Page';
import BtnAddNew from 'components/common/button/BtnAddNew';
import BtnEdit from 'components/common/button/BtnEdit';
import BtnDelete from 'components/common/button/BtnDelete';
import { Card, Row, Table, Popconfirm, Col } from 'antd';
import { typeDisciplines } from 'dump/data';

const columns = [
  {
    title: 'Loại xử lý',
    dataIndex: 'type',
    key: 'type',
  },
  {
    title: 'Mức xử lý',
    dataIndex: 'level',
    key: 'level',
  },
  {
    title: 'Thao tác',
    dataIndex: 'operation',
    render: (text, record) =>
      typeDisciplines.length >= 1 ? (
        <Fragment>
          <BtnEdit href={`#typeDisciplines/form/edit/${record.type}`} />
          <Popconfirm title="Sure to delete?" onConfirm={() => this.handleDelete(record.key)}>
            <BtnDelete />
          </Popconfirm>
        </Fragment>
      ) : null,
  },
];

class ListTypeDisciplinesPage extends Component {
  state = {};

  render() {
    return (
      <Card title="Xử lý kỷ luật">
        <Row>
          <Col span={24}>
            <BtnAddNew href="#/typeDisciplines/form/new" />
          </Col>
        </Row>
        <Row>
          <Col span={24}>
            <Table bordered dataSource={typeDisciplines} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <ListTypeDisciplinesPage />
  </Page>
);



